import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Shirt, Badge, Clock, CheckCircle } from "lucide-react";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";

const servicosDestaque = [
  {
    icon: <Shirt className="h-8 w-8 text-primary" />,
    title: "Lavagem de Roupas",
    description: "Lavagem cuidadosa para suas roupas do dia a dia, com produtos de alta qualidade.",
    image: "/images/service-1.jpg"
  },
  {
    icon: <Badge className="h-8 w-8 text-primary" />,
    title: "Lavagem de Uniformes",
    description: "Uniformes impecáveis, limpos e bem passados para seu ambiente de trabalho.",
    image: "/images/service-2.jpg"
  },
  {
    icon: <Clock className="h-8 w-8 text-primary" />,
    title: "Serviço Express",
    description: "Entrega no mesmo dia para pedidos realizados até às 10h da manhã.",
    image: "/images/service-3.jpg"
  },
];

const depoimentos = [
  {
    nome: "Maria Silva",
    texto: "Serviço de excelente qualidade! Sempre entregam no prazo e as roupas voltam impecáveis.",
    profissao: "Médica",
  },
  {
    nome: "João Oliveira",
    texto: "Uso há mais de 5 anos e nunca tive problemas. Preço justo e atendimento sensacional!",
    profissao: "Empresário",
  },
  {
    nome: "Ana Costa",
    texto: "A melhor lavanderia da cidade! Cuidam das minhas roupas como se fossem deles.",
    profissao: "Advogada",
  },
];

export default function Home() {
  return (
    <div className="flex flex-col">
      {/* Hero Section */}
      <section className="relative py-20 md:py-32">
        <div className="absolute inset-0 bg-gradient-to-r from-primary to-secondary opacity-90 z-[-1]" />
        <div className="container grid gap-8 md:grid-cols-2 items-center">
          <div className="space-y-6 text-center md:text-left animate-fade-in">
            <h1 className="text-4xl md:text-5xl font-bold tracking-tight text-white">
              Sua roupa limpa e cuidada com <span className="text-accent">rapidez</span> e <span className="text-accent">qualidade</span>
            </h1>
            <p className="text-xl text-white/90 max-w-md mx-auto md:mx-0">
              Deixe a Blitz Lavanderia cuidar das suas roupas enquanto você aproveita o seu tempo.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center md:justify-start">
              <Button asChild size="lg" className="bg-accent hover:bg-amber-400 text-primary-dark font-semibold animate-scale-in animate-delay-200">
                <Link href="/contato">Agendar Serviço</Link>
              </Button>
              <Button asChild variant="outline" size="lg" className="border-white text-white hover:bg-white/10 animate-scale-in animate-delay-300">
                <Link href="/servicos">Ver Serviços</Link>
              </Button>
            </div>
          </div>
          <div className="relative h-[300px] md:h-[400px] rounded-2xl overflow-hidden shadow-xl animate-slide-right">
            <Image
              src="/images/hero-banner.jpg"
              alt="Blitz Lavanderia"
              fill
              className="object-cover"
              priority
            />
          </div>
        </div>
      </section>

      {/* Serviços em Destaque */}
      <section className="py-16 bg-white">
        <div className="container space-y-12">
          <div className="text-center space-y-4 animate-fade-in">
            <h2 className="text-3xl font-bold text-primary">Nossos Serviços</h2>
            <p className="text-lg text-text-light max-w-2xl mx-auto">
              Conheça os serviços mais populares da Blitz Lavanderia
            </p>
          </div>
          
          <div className="grid md:grid-cols-3 gap-8">
            {servicosDestaque.map((servico, index) => (
              <Card key={index} className={`border-none shadow-md hover:shadow-lg transition-shadow card-hover overflow-hidden animate-fade-in animate-delay-${(index + 1) * 100}`}>
                <div className="relative h-48 w-full">
                  <Image
                    src={servico.image}
                    alt={servico.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <CardHeader className="bg-primary/5">
                  <div className="flex items-center gap-2">
                    <div className="text-primary">{servico.icon}</div>
                    <CardTitle className="text-primary">{servico.title}</CardTitle>
                  </div>
                </CardHeader>
                <CardContent className="pt-6">
                  <p className="text-text-light">{servico.description}</p>
                </CardContent>
                <CardFooter className="justify-center">
                  <Button asChild variant="ghost" className="gap-1 text-primary hover:text-primary-dark">
                    <Link href="/servicos">
                      Saiba mais
                      <span aria-hidden="true">→</span>
                    </Link>
                  </Button>
                </CardFooter>
              </Card>
            ))}
          </div>
          
          <div className="text-center animate-fade-in animate-delay-500">
            <Button asChild variant="outline" className="border-primary text-primary hover:bg-primary hover:text-white">
              <Link href="/servicos">Ver todos os serviços</Link>
            </Button>
          </div>
        </div>
      </section>

      {/* Diferenciais */}
      <section className="py-16 bg-stone-50">
        <div className="container">
          <div className="text-center space-y-4 mb-12 animate-fade-in">
            <h2 className="text-3xl font-bold text-primary">Por que escolher a Blitz?</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Conheça nossos diferenciais e por que somos a escolha certa para cuidar das suas roupas
            </p>
          </div>
          
          <div className="grid md:grid-cols-2 gap-8 items-center">
            <div className="space-y-6 order-2 md:order-1">
              <div className="flex gap-4 bg-white p-4 rounded-lg shadow-sm hover:shadow-md transition-shadow animate-slide-left">
                <CheckCircle className="h-6 w-6 text-primary flex-shrink-0 mt-1" />
                <div>
                  <h3 className="text-xl font-semibold text-primary">Profissionais Experientes</h3>
                  <p className="text-muted-foreground">Equipe treinada para cuidar das suas peças com atenção especial.</p>
                </div>
              </div>
              
              <div className="flex gap-4 bg-white p-4 rounded-lg shadow-sm hover:shadow-md transition-shadow animate-slide-left animate-delay-100">
                <CheckCircle className="h-6 w-6 text-primary flex-shrink-0 mt-1" />
                <div>
                  <h3 className="text-xl font-semibold text-primary">Produtos Premium</h3>
                  <p className="text-muted-foreground">Utilizamos produtos de alta qualidade que preservam as fibras e as cores das roupas.</p>
                </div>
              </div>
              
              <div className="flex gap-4 bg-white p-4 rounded-lg shadow-sm hover:shadow-md transition-shadow animate-slide-left animate-delay-200">
                <CheckCircle className="h-6 w-6 text-primary flex-shrink-0 mt-1" />
                <div>
                  <h3 className="text-xl font-semibold text-primary">Prazo de Entrega</h3>
                  <p className="text-muted-foreground">Cumprimos rigorosamente os prazos estabelecidos, com opção de serviço express.</p>
                </div>
              </div>
              
              <div className="flex gap-4 bg-white p-4 rounded-lg shadow-sm hover:shadow-md transition-shadow animate-slide-left animate-delay-300">
                <CheckCircle className="h-6 w-6 text-primary flex-shrink-0 mt-1" />
                <div>
                  <h3 className="text-xl font-semibold text-primary">Entrega em Domicílio</h3>
                  <p className="text-muted-foreground">Comodidade de receber suas roupas limpas e passadas no conforto da sua casa.</p>
                </div>
              </div>
            </div>
            
            <div className="relative h-[400px] rounded-lg overflow-hidden shadow-xl order-1 md:order-2 animate-slide-right">
              <Image
                src="/images/workers.jpg"
                alt="Profissionais da Blitz Lavanderia"
                fill
                className="object-cover"
              />
            </div>
          </div>
        </div>
      </section>
      
      {/* Depoimentos */}
      <section className="py-16 bg-white">
        <div className="container space-y-12">
          <div className="text-center space-y-4">
            <h2 className="text-3xl font-bold">O que nossos clientes dizem</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Veja os depoimentos de quem confia na Blitz Lavanderia
            </p>
          </div>
          
          <Carousel className="w-full max-w-4xl mx-auto">
            <CarouselContent>
              {depoimentos.map((depoimento, index) => (
                <CarouselItem key={index}>
                  <Card className="border-none shadow-sm bg-stone-50 h-full">
                    <CardContent className="pt-8">
                      <blockquote className="text-center space-y-4">
                        <p className="text-lg italic">"{depoimento.texto}"</p>
                        <footer className="mt-4">
                          <p className="font-semibold">{depoimento.nome}</p>
                          <p className="text-sm text-muted-foreground">{depoimento.profissao}</p>
                        </footer>
                      </blockquote>
                    </CardContent>
                  </Card>
                </CarouselItem>
              ))}
            </CarouselContent>
            <CarouselPrevious className="left-0" />
            <CarouselNext className="right-0" />
          </Carousel>
        </div>
      </section>
      
      {/* CTA Final */}
      <section className="py-16 bg-primary text-primary-foreground relative overflow-hidden">
        <div className="absolute inset-0 bg-[url('/blitz_lavanderia_imagens_avulsas (3).jpg')] bg-cover bg-center opacity-10"></div>
        <div className="container relative z-10 text-center space-y-6 animate-fade-in">
          <h2 className="text-3xl font-bold">Pronto para experimentar nossos serviços?</h2>
          <p className="text-lg max-w-2xl mx-auto">
            Entre em contato hoje mesmo e descubra como podemos facilitar sua vida com nossos serviços de lavanderia.
          </p>
          <div className="flex gap-4 flex-col sm:flex-row justify-center">
            <Button asChild size="lg" variant="secondary" className="bg-white text-primary hover:bg-white/90 animate-scale-in animate-delay-100">
              <Link href="/contato">Agendar Coleta</Link>
            </Button>
            <Button asChild size="lg" variant="outline" className="border-white text-white hover:bg-white/10 animate-scale-in animate-delay-200">
              <Link href="/servicos">Conhecer Serviços</Link>
            </Button>
          </div>
        </div>
      </section>
    </div>
  );
} 